.Footer{display:flex;align-items:center;width:100%;height:145px;background-color:#b91616}.Footer__Content{width:100%;display:flex;align-items:center;justify-content:space-between;color:#fff}.Footer__Title{font-family:"Geometria";font-style:normal;font-weight:500;font-size:24px;line-height:30px}.Footer__Contacts{width:100%;max-width:684px;display:flex;justify-content:space-around}.Footer__ContactsItem{font-family:"KyivType Sans";font-style:normal;font-weight:500;font-size:18px;line-height:22px;color:#fff}.Footer__Button{font-family:"Geometria";font-style:normal;font-weight:500;font-size:18px;line-height:23px}.Footer__Button:hover{cursor:pointer;text-decoration:underline}.Footer__Button::after{content:"↑";margin-left:25px;font-family:"Times New Roman",Times,serif;position:absolute;color:#fff}@media(max-width: 1440px){.Footer{height:80px}.Footer__Title{font-size:20px;line-height:23px}.Footer__Button{font-weight:500;font-size:16px}.Footer__Button::after{margin-left:5px}.Footer__Contacts{max-width:600px}.Footer__ContactsItem{font-size:14px;line-height:15px}}@media(max-width: 700px){.Footer{height:60px}.Footer__Title{font-size:10px;line-height:13px}.Footer__Button{font-weight:500;font-size:8px}.Footer__Button::after{margin-left:5px}.Footer__Contacts{max-width:228px}.Footer__ContactsItem{font-size:6px;line-height:7px;padding:0px 5px}}