@import '../../styles/colors.scss';

.Navbar {
    width: 100%;
    height: 60px;
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: space-between;
    background-color: $red;
    z-index: 100;

    &__Link {
        margin-top: 44px;

        &:first-child {
            margin-top: 0px;
        }
    }

    &__Links {
        position: absolute;
        width: 100%;
        top: 60px;
        height: 100vh;
        z-index: -1;
        padding-top: 160px;
        padding-left: 22px;
        background-color: $red;
        display: none;
        flex-direction: column;
        align-items: flex-start;
    }

    &__Logo {
        padding-left: 20px;
    }

    &__Button {
        padding-right: 10px;
    }
}